import { useResizeContext } from '@blacktokki/core';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationProp } from '@react-navigation/stack';
import React from 'react';

import { parseHtmlToSections } from '../../components/HeaderSelectBar';
import { getNoteLinks, SearchBar } from '../../components/SearchBar';
import { useNotePages } from '../../hooks/useNoteStorage';
import { NavigationParamList } from '../../types';
import { NoteListSection } from './NoteListSection';
import { getSplitTitle, sectionDescription } from './NotePageScreen';

function findEmptyLists(html: string): string[] {
  const regex = /<(ol|ul)\b[^>]*>([\s\n\r]*)<\/\1>/gi;
  const matches: string[] = [];
  let match;

  while ((match = regex.exec(html)) !== null) {
    matches.push(match[0]); //   
  }

  return matches;
}

export const ProblemsScreen: React.FC = () => {
  const _window = useResizeContext();
  const navigation = useNavigation<StackNavigationProp<NavigationParamList>>();
  const { data: pages = [], isLoading } = useNotePages();
  const unknownData = getNoteLinks(pages)
    .filter((v) => {
      const page = pages.find((v2) => v2.title === v.title);
      if (page?.description) {
        if (
          v.section === undefined ||
          parseHtmlToSections(page.description).find((v2) => v2.title === v.section)
        ) {
          return false;
        }
      }
      return true;
    })
    .map((v) => ({
      ...v,
      subtitle:
        (v.section === undefined ? 'Unknown note link' : 'Unknown section link') + `(${v.origin})`,
    }));
  const emptyParentData = pages
    .map((v) => {
      const splitTitle = getSplitTitle(v.title);
      if (
        v.description &&
        splitTitle.length === 2 &&
        pages.find((v) => v.title === splitTitle[0]) === undefined
      ) {
        return { title: splitTitle[0], subtitle: `Empty parent note(${v.title})` };
      }
      return undefined;
    })
    .filter((v) => v !== undefined);

  const emptyContentdata = pages.flatMap((v) => {
    const paragraph = parseHtmlToSections(v.description || '');
    const emptyParagraph = paragraph.filter(
      (v2) => v2.level !== 0 && sectionDescription(paragraph, v2.title, false).trim() === ''
    );
    const emptyList = paragraph.filter((v2) => findEmptyLists(v2.description).length > 0);
    return [
      ...emptyParagraph.map((v2) => ({
        title: v.title,
        section: v2.level === 0 ? undefined : v2.title,
        subtitle: 'Empty section',
      })),
      ...emptyList.map((v2) => ({
        title: v.title,
        section: v2.level === 0 ? undefined : v2.title,
        subtitle: 'Empty list',
      })),
    ];
  });
  return (
    <>
      {_window === 'portrait' && <SearchBar />}
      <NoteListSection
        contents={[...unknownData, ...emptyParentData, ...emptyContentdata]}
        onPress={(title) => navigation.push('EditPage', { title })}
        isLoading={isLoading}
        emptyMessage="   ."
      />
    </>
  );
};
